.demo-nav {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 2.38933rem;
  background-color: #ffffff;
}
.demo-nav__back {
  position: absolute;
  top: 0.68267rem;
  left: 0.68267rem;
  font-size: 1.024rem;
  color: #969799;
  cursor: pointer;
}
.demo-nav__title {
  font-size: 0.68267rem;
  font-weight: 600;
  text-transform: capitalize;
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
